import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthenComponent } from '../../layouts/authen/authen.component';
//Routes trang admin
const routes_authen: Routes = [
  {
    path: 'authen',
    component: AuthenComponent,
    children: [
        {
            path: '',
            loadChildren: () => import('../../modules/general/about/about.module')
            .then(mod => mod.AboutModule)      
        },  
        {
          path: 'list-contact',
          loadChildren: () => import('../../modules/general/contact/contact.module')
          .then(mod => mod.ContactModule)
      },   
    ]
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes_authen, { useHash: true, enableTracing: true })],
  exports: [RouterModule],
  declarations: []
})
export class AppRoutingAuthenModule { }