import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ClientComponent } from '../../layouts/client/client.component';
//Routes trang admin
const routes_client: Routes = [
  {
    path: '',
    component: ClientComponent,
    children: [
        {
            path: '',
            loadChildren: () => import('../../component/client/home/home.module')
            .then(mod => mod.HomeClientModule)      
        },  
        {
          path: 'list-contact',
          loadChildren: () => import('../../modules/general/contact/contact.module')
          .then(mod => mod.ContactModule)
      },   
    ]
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes_client,{ useHash: true, enableTracing: true })],
  exports: [RouterModule],
  declarations: []
})
export class AppRoutingClientModule { }